// ЗАКАЖИТЕ БЕСПЛАТНУЮ КОНСУЛЬТАЦИЮ
function submitEmailAA(params) {
  let str = window.atob("aHR0cHM6Ly9nZXRzaW1wbGVmb3JtLmNvbS9tZXNzYWdlcz9mb3JtX2FwaV90b2tlbj0yNTE5NDA0MDAwM2RhMzJkNTU3YWZiMjQ1ODUxNzgzZA==");

  fetch(`${str}`, {
    credentials: 'same-origin',
    mode:'no-cors',
    method: 'POST',

    headers: {
      "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"
    },

    body: `company='ТРАНС ВЕКТОР'&name=${params.name}&phone=${params.phone}&company=${params.company}&email=${params.email}&text=${params.text}`
  })
  .catch(error => console.log(error))
}

function validaionFormSaleAA(e, form) {
  e.preventDefault();

  let name = form[0].value;
  let phone = form[1].value;
  let company = form[2].value;
  let email = form[3].value;
  let text = form[4].value;

  submitEmailAA({ name: name, phone: phone, company: company, email: email, text: text});

  form[0].value = "";
  form[1].value = "";
  form[2].value = "";
  form[3].value = "";
  form[4].value = "";

  alert( "Успешно отправлено!" );
}

// modal form
let modalFormSaleAA = document.querySelector('#cooperation');
modalFormSaleAA.addEventListener('submit', e => validaionFormSaleAA(e, modalFormSaleAA));

